export enum CASES_STATUS {
  OPEN = '1',
  IN_PROCESS = '2',
  WAITING_SOLUTION = '3',
  CLOSED = '4',
  WATTING_RCI = '5',
  CANCEL = '6',
  INCOMPLETE = '7',
}

export type CasesStatusType =
  | CASES_STATUS.OPEN
  | CASES_STATUS.IN_PROCESS
  | CASES_STATUS.WAITING_SOLUTION
  | CASES_STATUS.CLOSED
  | CASES_STATUS.WATTING_RCI
  | CASES_STATUS.CANCEL
  | CASES_STATUS.INCOMPLETE;
