import { Pipe, PipeTransform } from '@angular/core';
import { CaseItemCategory } from '@core/models';
import { ClientType } from '@core/constants/client-type.constants';

@Pipe({
  name: 'filterByClientType',
})
export class FilterByClientTypePipe implements PipeTransform {
  constructor() {}

  transform(value: CaseItemCategory[], clientType: ClientType) {
    return value?.filter((it) => !it.disableOptionFor?.some((type) => type === clientType));
  }
}
