import { AddressType } from './../core/config/address-type';
import { HeaderService } from '@core/services/header/header.service';
import { CASE_DOCUMENTS } from './../core/config/sub-cases';
import {
  DocumentsTypes,
  DOCUMENTS_PROVIDE_TYPES,
  DOCUMENTS_REQUEST_TYPES,
} from '@core/constants/documents-types.constants';
import { CASE_CANCELLATIONS } from '@core/config/sub-cases';
import { Option } from '@core/models/option';
import { CasePayload } from '@api/models/case-payload.model';
import { DictionaryService } from '@core/services/dictionary/dictionary.service';
import { CaseType, CASES } from '@core/config/cases';
import { Case } from '@api/models/case.model';
import { CaseService } from '@core/services/case/case.service';
import { Component, OnInit, Inject, OnDestroy, ViewChild, TemplateRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators, AbstractControl } from '@angular/forms';
import { Subscription, Observable, combineLatest } from 'rxjs';
import { ResponseAvailableProductProductList } from '@api/models/responseAvailableProductProductList';
import {
  alphabeticRegExp,
  emailRegExp,
  spanishPhoneRegExp,
  spanishPostalCodeRegExp,
  nameAlphabeticRegExp,
} from '@shared/utils';
import { noWhiteSpaceValidator } from '@shared/validators';
import { Cnogroupcnocnae } from '@api/models/cnogroupCnoCnae';
import {
  IncomeSource,
  INCOME_SOURCE,
  CASES_ITEMS,
  STREET_TYPES,
  StreetTypes,
} from '@core/constants';
import {
  distinctUntilChanged,
  filter,
  delay,
  startWith,
  switchMap,
  map,
  tap,
} from 'rxjs/operators';
import { ibanValidator } from '@shared/validators/iban.validator';
import { CASE_CONTRACTUAL_MODIF } from '@core/config/sub-cases';
import { CASES_STATUS } from '@core/config/cases-status';
import { FORM_CASE } from '@core/config/form-case';
import { REASON_CANCELLATIONT_LIST } from '@core/constants/reason-cancellation.constants';
import { CaseItem } from '@core/models';
import { DatepickerDateCustomClasses, BsLocaleService } from 'ngx-bootstrap/datepicker';
import { AddressLocation } from '@api/models/addressLocation';
import { ErrorHandlerService } from '@core/services/errors/error-handler.service';
import { GlobalPositionService } from '@core/services/global-position/global-position.service';
import { findAccount, findDeposit } from '@core/utils';
import { formatDate } from '@angular/common';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ADDRESS_TYPES } from '@core/constants/address-type.constants';
import { AccountService } from '@core/services/account/account.service';

@Component({
  selector: 'app-cases',
  templateUrl: './cases.component.html',
  styles: [],
})
export class CasesComponent implements OnInit, OnDestroy {
  @ViewChild('modalSucces', { static: false }) modalSucces: TemplateRef<any>;
  modalRef: BsModalRef;
  // Constants
  readonly MINLENGTH = 3;
  readonly MAXLENGTH = 25;
  readonly validationDates = {
    minExpiration: new Date(),
  } as const;
  dateCustomClasses: DatepickerDateCustomClasses[];

  state$ = this.createState();

  private subs = new Subscription();
  casesListOpen: Case[];
  casesListInProces: Case[];
  casesListClosed: Case[];
  caseTypeSelected: CaseItem;
  caseSelected: any;
  isProductType = false;
  caseForm: FormGroup;
  allProductsList: ResponseAvailableProductProductList[];
  productsList: ResponseAvailableProductProductList[];
  employmentSituation$: Observable<Cnogroupcnocnae[]>;
  cno$: Observable<Cnogroupcnocnae[]>;
  cnae$: Observable<Cnogroupcnocnae[]>;
  locationList$: Observable<AddressLocation[]>;
  showProdutcSelector: boolean;
  showNameAndLastnameFields: boolean;
  showPhoneAndMailFields: boolean;
  showIbanFields: boolean;
  showLaborDataFields: boolean;
  showCancellationsFields: boolean;
  showCancellationTaxationFields: boolean;
  showAddressDataFields: boolean;
  showPolicyDataFields: boolean;
  showProvideDocumentsFiels: boolean;
  showRequestDocumentsFiels: boolean;
  customerArea: boolean;
  formSubmit: boolean;
  newCaseMobile = false;
  showAllProducts = false;

  constructor(
    private headerService: HeaderService,
    private formBuilder: FormBuilder,
    private caseService: CaseService,
    private dictionaryService: DictionaryService,
    private bsLocaleService: BsLocaleService,
    private errorHandler: ErrorHandlerService,
    private globalPositionService: GlobalPositionService,
    private modalService: BsModalService,
    private accountService: AccountService,
    @Inject(CASES_ITEMS) public casesItems: CaseItem[],
    @Inject(INCOME_SOURCE) public incomeSource: IncomeSource,
    @Inject(REASON_CANCELLATIONT_LIST) public ReasonCancellationtList: Option[],
    @Inject(ADDRESS_TYPES) public addressTypes: AddressType,
    @Inject(STREET_TYPES) public streetTypes: StreetTypes,
    @Inject(DOCUMENTS_PROVIDE_TYPES) public documentsProvideTypes: DocumentsTypes,
    @Inject(DOCUMENTS_REQUEST_TYPES) public documentsRequestTypes: DocumentsTypes
  ) {
    const now = new Date();
    this.dateCustomClasses = [{ date: now, classes: [] }];
  }

  ngOnInit(): void {
    this.headerService.updateTitle('menu_requests');
    this.bsLocaleService.use('es');
    this.createForm();
    this.getCases();
    this.getProducts();
    this.getAllProducts();
    this.setStreamValues();
    this.locationList$ = this.setLocationStream();
    this.cleanLocationCodeWhenPostalCodeChange();
    this.listenEmploymentSituationChanges();
    this.listenIbanChanges();
  }

  private createForm() {
    this.caseForm = this.formBuilder.group({
      typeCase: [null, Validators.required],
      category: [null, Validators.required],
      subject: [null, Validators.required],
      coments: [null, [Validators.required, Validators.maxLength(1000)]],
      product: [null],
      name: [null],
      firstSurname: [null],
      secondarySurname: [null],
      email: [null],
      phone: [null],
      employmentSituation: [null],
      cnae: [{ value: null, disabled: true }],
      cno: [{ value: null, disabled: true }],
      freelanceDateRegister: [{ value: null, disabled: true }],
      iban: [null],
      reasonCancellation: [null],
      cancellationTaxation: [null],
      addressType: [null],
      streetType: [null],
      streetName: [null],
      streetNumber: [null],
      portal: [null],
      floor: [null],
      door: [null],
      postalCode: [null],
      locationCode: [null],
      offerComunications: [null],
      transferData: [null],
      documentType: [null],
    });

    this.f.category.disable();
    this.typeCaseValueChange();
    this.categoryValueChange();
    this.employmentSituationValuechange();
  }

  get f() {
    return this.caseForm.controls;
  }

  private getCases() {
    this.subs.add(
      this.caseService.getCases().subscribe(
        (result) => {
          this.casesListOpen = result
            ? result.filter((item) => item.status === CASES_STATUS.OPEN)
            : [];
          this.casesListInProces = result
            ? result.filter(
                (item) =>
                  item.status !== CASES_STATUS.OPEN &&
                  item.status !== CASES_STATUS.CLOSED &&
                  item.status !== CASES_STATUS.CANCEL
              )
            : [];
          this.casesListClosed = result
            ? result.filter(
                (item) => item.status === CASES_STATUS.CLOSED || item.status === CASES_STATUS.CANCEL
              )
            : [];
        },
        (error) => {
          this.errorHandler.showError(error);
        }
      )
    );
  }

  getTypeCaseTitle(caseType: CaseType) {
    const itmCase = this.casesItems.filter((c) => c.id === caseType)[0];
    return itmCase.title;
  }

  selectCaseType($event) {
    this.f.category.setValue(null);
    if ($event) {
      this.caseTypeSelected = this.casesItems.filter((c) => c.id === this.f.typeCase.value)[0];
      this.f.category.enable();
    } else {
      this.f.category.disable();
    }
  }

  private typeCaseValueChange() {
    this.caseForm.get(FORM_CASE.TYPE_CASE).valueChanges.subscribe((value) => {
      this.formSubmit = false;
      this.cleanComentsAndSubject();
      this.showAllProducts = value === CASES.GEN_INFORMATION;
      this.setGenInformation(value === CASES.GEN_INFORMATION || value === CASES.HIRING);
      this.setCustomerArea(value === CASES.CUSTOMER_AREA);
      this.setCancellation(value === CASES.CANCELLATIONS);
    });
  }

  private setGenInformation(isTrue: boolean) {
    if (isTrue) {
      this.caseForm.get(FORM_CASE.PRODUCT).setValue(null);
      this.caseForm.get(FORM_CASE.PRODUCT).setValidators([Validators.required]);
      this.showProdutcSelector = true;
    } else {
      this.caseForm.get(FORM_CASE.PRODUCT).setValue(null);
      this.caseForm.get(FORM_CASE.PRODUCT).clearValidators();
      this.showProdutcSelector = false;
    }
    this.caseForm.get(FORM_CASE.PRODUCT).updateValueAndValidity();
  }

  private setCustomerArea(isTrue: boolean) {
    if (isTrue) {
      this.caseForm.get(FORM_CASE.IBAN).setValidators([Validators.required, ibanValidator]);
      this.customerArea = true;
    } else {
      this.caseForm.get(FORM_CASE.IBAN).setValue(null);
      this.caseForm.get(FORM_CASE.IBAN).clearValidators();
      this.customerArea = false;
    }
    this.caseForm.get(FORM_CASE.IBAN).updateValueAndValidity();
  }

  private setCancellation(siTrue: boolean) {
    if (siTrue) {
      this.caseForm.get(FORM_CASE.IBAN).setValidators([Validators.required, ibanValidator]);
      this.caseForm.get(FORM_CASE.REASON_CANCELLATION).setValidators([Validators.required]);
      this.showCancellationsFields = true;
    } else {
      this.caseForm.get(FORM_CASE.IBAN).setValue(null);
      this.caseForm.get(FORM_CASE.REASON_CANCELLATION).setValue(null);
      this.caseForm.get(FORM_CASE.IBAN).clearValidators();
      this.caseForm.get(FORM_CASE.REASON_CANCELLATION).clearValidators();
      this.showCancellationsFields = false;
    }
    this.caseForm.get(FORM_CASE.REASON_CANCELLATION).updateValueAndValidity();
  }

  private categoryValueChange() {
    this.caseForm.get(FORM_CASE.CATEGORY).valueChanges.subscribe((value) => {
      this.formSubmit = false;
      this.cleanComentsAndSubject();
      // Nombre y Apellidos
      this.setNameAndSurname(value === CASE_CONTRACTUAL_MODIF.NAME_AND_SURNAME);
      // Datos laborales
      this.setLaboralData(value === CASE_CONTRACTUAL_MODIF.LABOR_DATA);
      // Datos de contacto (tfno/mail)
      this.setContactPhoneMail(value === CASE_CONTRACTUAL_MODIF.CONTACT_DETAILS_PHONE_MAIL);
      // Cambios direccion
      this.setAddress(value === CASE_CONTRACTUAL_MODIF.ADDRESS_CHANGES);
      // Cambios politicas de cocentimiento
      this.setPrivacyPolicyOrConsents(value === CASE_CONTRACTUAL_MODIF.PRIVACY_POLICY_OR_CONSENTS);
      // Cambios Cta Bancaria
      this.setBankAccount(value === CASE_CONTRACTUAL_MODIF.BANK_ACCOUNT_CHANGES);
      // Aportar docuementacion
      this.setProvideContractualDocuments(value === CASE_DOCUMENTS.PROVIDE_DOCUMENTS);
      // Solicitar docuementacion
      this.setRequestContractualDocuments(value === CASE_DOCUMENTS.REQUEST_CONTRACTUAL_DOCUMENTS);
      // Cancelaciones
      this.cancellationOfTaxation(value === CASE_CANCELLATIONS.CANCELLATION_OF_TAXATION);
    });
  }

  private setNameAndSurname(isTrue: boolean) {
    const nameValidators = [
      Validators.minLength(this.MINLENGTH),
      Validators.maxLength(this.MAXLENGTH),
      Validators.pattern(nameAlphabeticRegExp),
      noWhiteSpaceValidator,
    ];
    const surnameValidators = [
      Validators.minLength(this.MINLENGTH),
      Validators.maxLength(this.MAXLENGTH),
      Validators.pattern(alphabeticRegExp),
      noWhiteSpaceValidator,
    ];

    if (isTrue) {
      this.caseForm.get(FORM_CASE.NAME).setValidators([Validators.required, ...nameValidators]);
      this.caseForm
        .get(FORM_CASE.FIRST_SURNAME)
        .setValidators([Validators.required, ...surnameValidators]);
      this.caseForm.get(FORM_CASE.SECONDARY_SURNAME).setValidators([...surnameValidators]);
      this.showNameAndLastnameFields = true;
    } else {
      this.caseForm.get(FORM_CASE.NAME).setValue(null);
      this.caseForm.get(FORM_CASE.FIRST_SURNAME).setValue(null);
      this.caseForm.get(FORM_CASE.SECONDARY_SURNAME).setValue(null);
      this.caseForm.get(FORM_CASE.NAME).clearValidators();
      this.caseForm.get(FORM_CASE.FIRST_SURNAME).clearValidators();
      this.caseForm.get(FORM_CASE.SECONDARY_SURNAME).clearValidators();
      this.showNameAndLastnameFields = false;
    }

    this.caseForm.get(FORM_CASE.NAME).updateValueAndValidity();
    this.caseForm.get(FORM_CASE.FIRST_SURNAME).updateValueAndValidity();
    this.caseForm.get(FORM_CASE.SECONDARY_SURNAME).updateValueAndValidity();
  }

  private setLaboralData(isTrue: boolean) {
    if (isTrue) {
      this.caseForm.get(FORM_CASE.EMPLOYMENT_SITUATION).setValidators([Validators.required]);
      this.caseForm.get(FORM_CASE.CNAE).setValidators([Validators.required]);
      this.caseForm.get(FORM_CASE.CNO).setValidators([Validators.required]);
      this.showLaborDataFields = true;
    } else {
      this.caseForm.get(FORM_CASE.EMPLOYMENT_SITUATION).setValue(null);
      this.caseForm.get(FORM_CASE.CNAE).setValue(null);
      this.caseForm.get(FORM_CASE.CNO).setValue(null);
      this.caseForm.get(FORM_CASE.EMPLOYMENT_SITUATION).clearValidators();
      this.caseForm.get(FORM_CASE.CNAE).clearValidators();
      this.caseForm.get(FORM_CASE.CNO).clearValidators();
      this.showLaborDataFields = false;
    }
    this.caseForm.get(FORM_CASE.EMPLOYMENT_SITUATION).updateValueAndValidity();
    this.caseForm.get(FORM_CASE.CNAE).updateValueAndValidity();
    this.caseForm.get(FORM_CASE.CNO).updateValueAndValidity();
  }

  private setContactPhoneMail(isTrue: boolean) {
    if (isTrue) {
      this.caseForm
        .get(FORM_CASE.EMAIL)
        .setValidators([Validators.pattern(emailRegExp), noWhiteSpaceValidator]);
      this.caseForm
        .get(FORM_CASE.PHONE)
        .setValidators([Validators.pattern(spanishPhoneRegExp), noWhiteSpaceValidator]);
      this.showPhoneAndMailFields = true;
    } else {
      this.caseForm.get(FORM_CASE.EMAIL).setValue(null);
      this.caseForm.get(FORM_CASE.PHONE).setValue(null);
      this.caseForm.get(FORM_CASE.EMAIL).clearValidators();
      this.caseForm.get(FORM_CASE.PHONE).clearValidators();
      this.showPhoneAndMailFields = false;
    }
    this.caseForm.get(FORM_CASE.EMAIL).updateValueAndValidity();
    this.caseForm.get(FORM_CASE.PHONE).updateValueAndValidity();
  }

  private setAddress(isTrue: boolean) {
    if (isTrue) {
      this.caseForm.get(FORM_CASE.ADDRESS_TYPE).setValidators([Validators.required]);
      this.caseForm.get(FORM_CASE.STREET_TYPE).setValidators([Validators.required]);
      this.caseForm
        .get(FORM_CASE.STREET_NAME)
        .setValidators([
          Validators.required,
          Validators.minLength(3),
          Validators.maxLength(200),
          noWhiteSpaceValidator,
        ]);
      this.caseForm
        .get(FORM_CASE.STREET_NUMBER)
        .setValidators([Validators.maxLength(4), noWhiteSpaceValidator]);
      this.caseForm
        .get(FORM_CASE.PORTAL)
        .setValidators([Validators.maxLength(2), noWhiteSpaceValidator]);
      this.caseForm
        .get(FORM_CASE.FLOOR)
        .setValidators([Validators.maxLength(4), noWhiteSpaceValidator]);
      this.caseForm
        .get(FORM_CASE.DOOR)
        .setValidators([Validators.maxLength(9), noWhiteSpaceValidator]);
      this.caseForm
        .get(FORM_CASE.POSTAL_CODE)
        .setValidators([
          Validators.required,
          Validators.pattern(spanishPostalCodeRegExp),
          Validators.maxLength(5),
          Validators.minLength(5),
          noWhiteSpaceValidator,
        ]);
      this.caseForm.get(FORM_CASE.LOCATION_CODE).setValidators([Validators.required]);
      this.showAddressDataFields = true;
    } else {
      this.caseForm.get(FORM_CASE.ADDRESS_TYPE).setValue(null);
      this.caseForm.get(FORM_CASE.STREET_TYPE).setValue(null);
      this.caseForm.get(FORM_CASE.STREET_NAME).setValue(null);
      this.caseForm.get(FORM_CASE.STREET_NUMBER).setValue(null);
      this.caseForm.get(FORM_CASE.PORTAL).setValue(null);
      this.caseForm.get(FORM_CASE.FLOOR).setValue(null);
      this.caseForm.get(FORM_CASE.DOOR).setValue(null);
      this.caseForm.get(FORM_CASE.POSTAL_CODE).setValue(null);
      this.caseForm.get(FORM_CASE.LOCATION_CODE).setValue(null);
      this.caseForm.get(FORM_CASE.STREET_TYPE).clearValidators();
      this.caseForm.get(FORM_CASE.STREET_NAME).clearValidators();
      this.caseForm.get(FORM_CASE.STREET_NUMBER).clearValidators();
      this.caseForm.get(FORM_CASE.PORTAL).clearValidators();
      this.caseForm.get(FORM_CASE.FLOOR).clearValidators();
      this.caseForm.get(FORM_CASE.DOOR).clearValidators();
      this.caseForm.get(FORM_CASE.POSTAL_CODE).clearValidators();
      this.caseForm.get(FORM_CASE.LOCATION_CODE).clearValidators();
      this.showAddressDataFields = false;
    }

    this.caseForm.get(FORM_CASE.ADDRESS_TYPE).updateValueAndValidity();
    this.caseForm.get(FORM_CASE.STREET_TYPE).updateValueAndValidity();
    this.caseForm.get(FORM_CASE.STREET_NAME).updateValueAndValidity();
    this.caseForm.get(FORM_CASE.STREET_NUMBER).updateValueAndValidity();
    this.caseForm.get(FORM_CASE.PORTAL).updateValueAndValidity();
    this.caseForm.get(FORM_CASE.FLOOR).updateValueAndValidity();
    this.caseForm.get(FORM_CASE.DOOR).updateValueAndValidity();
    this.caseForm.get(FORM_CASE.POSTAL_CODE).updateValueAndValidity();
    this.caseForm.get(FORM_CASE.LOCATION_CODE).updateValueAndValidity();
  }

  private setPrivacyPolicyOrConsents(isTrue: boolean) {
    if (isTrue) {
      this.caseForm.get(FORM_CASE.OFFER_COMUNICATIONS).setValidators([Validators.required]);
      this.caseForm.get(FORM_CASE.TRANSFER_DATA).setValidators([Validators.required]);
      this.showPolicyDataFields = true;
    } else {
      this.caseForm.get(FORM_CASE.OFFER_COMUNICATIONS).setValue(null);
      this.caseForm.get(FORM_CASE.TRANSFER_DATA).setValue(null);
      this.caseForm.get(FORM_CASE.OFFER_COMUNICATIONS).clearValidators();
      this.caseForm.get(FORM_CASE.TRANSFER_DATA).clearValidators();
      this.showPolicyDataFields = false;
    }
    this.caseForm.get(FORM_CASE.OFFER_COMUNICATIONS).updateValueAndValidity();
    this.caseForm.get(FORM_CASE.TRANSFER_DATA).updateValueAndValidity();
  }

  private setBankAccount(isTrue: boolean) {
    if (isTrue) {
      this.caseForm.get(FORM_CASE.IBAN).setValidators([Validators.required, ibanValidator]);
      this.showIbanFields = true;
    } else {
      this.caseForm.get(FORM_CASE.IBAN).setValue(null);
      this.caseForm.get(FORM_CASE.IBAN).clearValidators();
      this.showIbanFields = false;
    }
    this.caseForm.get(FORM_CASE.IBAN).updateValueAndValidity();
  }

  private setProvideContractualDocuments(isTrue: boolean) {
    if (isTrue) {
      this.caseForm.get(FORM_CASE.DOCUMENT_TYPE).setValidators([Validators.required]);
      this.showProvideDocumentsFiels = true;
    } else {
      this.caseForm.get(FORM_CASE.DOCUMENT_TYPE).setValue(null);
      this.caseForm.get(FORM_CASE.DOCUMENT_TYPE).clearValidators();
      this.showProvideDocumentsFiels = false;
    }
    this.caseForm.get(FORM_CASE.DOCUMENT_TYPE);
  }

  private setRequestContractualDocuments(isTrue: boolean) {
    if (isTrue) {
      this.caseForm.get(FORM_CASE.DOCUMENT_TYPE).setValidators([Validators.required]);
      this.showRequestDocumentsFiels = true;
    } else {
      this.caseForm.get(FORM_CASE.DOCUMENT_TYPE).setValue(null);
      this.caseForm.get(FORM_CASE.DOCUMENT_TYPE).clearValidators();
      this.showRequestDocumentsFiels = false;
    }
    this.caseForm.get(FORM_CASE.DOCUMENT_TYPE);
  }

  private cancellationOfTaxation(isTrue: boolean) {
    if (isTrue) {
      this.caseForm.get(FORM_CASE.CANCELLATION_TAXATION).setValidators([Validators.required]);
      this.showCancellationTaxationFields = true;
    } else {
      this.caseForm.get(FORM_CASE.CANCELLATION_TAXATION).setValue(null);
      this.caseForm.get(FORM_CASE.CANCELLATION_TAXATION).clearValidators();
      this.showCancellationTaxationFields = false;
    }
    this.caseForm.get(FORM_CASE.CANCELLATION_TAXATION).updateValueAndValidity();
  }

  private cleanComentsAndSubject() {
    this.caseForm.get(FORM_CASE.SUBJECT).setValue(null);
    this.caseForm.get(FORM_CASE.SUBJECT).updateValueAndValidity();
    this.caseForm.get(FORM_CASE.COMENTS).setValue(null);
    this.caseForm.get(FORM_CASE.COMENTS).updateValueAndValidity();
  }

  get emailOrPhoneAtLeastOneError() {
    const validMail = this.f.email.value === null || this.f.email.value === '';
    const validPhone = this.f.phone.value === null || this.f.phone.value === '';
    return this.formSubmit && validMail && validPhone;
  }

  private employmentSituationValuechange() {
    this.caseForm.get(FORM_CASE.EMPLOYMENT_SITUATION).valueChanges.subscribe((value) => {
      if (value === '01' || value === '02') {
        this.caseForm.get(FORM_CASE.CNAE).setValidators([Validators.required]);
        this.caseForm.get(FORM_CASE.CNO).setValidators([Validators.required]);
      } else {
        this.caseForm.get(FORM_CASE.CNAE).setValue(null);
        this.caseForm.get(FORM_CASE.CNO).setValue(null);
        this.caseForm.get(FORM_CASE.CNAE).clearValidators();
        this.caseForm.get(FORM_CASE.CNO).clearValidators();
      }
      if (value === '02') {
        this.caseForm.get(FORM_CASE.FREELANCE_DATE_REGISTER).enable();
      } else {
        this.caseForm.get(FORM_CASE.FREELANCE_DATE_REGISTER).setValue(null);
        this.caseForm.get(FORM_CASE.FREELANCE_DATE_REGISTER).disable();
      }
      this.caseForm.get(FORM_CASE.CNAE).updateValueAndValidity();
      this.caseForm.get(FORM_CASE.CNO).updateValueAndValidity();
      this.caseForm.get(FORM_CASE.FREELANCE_DATE_REGISTER).updateValueAndValidity();
    });
  }

  private getAllProducts() {
    this.subs.add(
      this.accountService.currentCustomer$
        .pipe(
          filter((response) => !!response),
          switchMap((response) =>
            this.dictionaryService.getProducts(response?.DatosPersonales?.mail)
          )
        )
        .subscribe((products) => {
          this.allProductsList = products.productList;
        })
    );
  }

  private getProducts() {
    this.subs.add(
      this.dictionaryService.getProducts().subscribe((products) => {
        this.productsList = products.productList;
      })
    );
  }

  private setLocationStream() {
    const postalCode = this.caseForm.get(FORM_CASE.POSTAL_CODE);

    return postalCode.valueChanges.pipe(
      startWith(postalCode.value),
      filter(() => postalCode.valid && postalCode.value !== null),
      switchMap((value: string) => this.dictionaryService.getLocation(value))
    );
  }

  private cleanLocationCodeWhenPostalCodeChange() {
    this.caseForm.get(FORM_CASE.POSTAL_CODE).valueChanges.subscribe((value) => {
      if (this.caseForm.get(FORM_CASE.LOCATION_CODE).value) {
        this.caseForm.get(FORM_CASE.LOCATION_CODE).setValue(null);
        this.caseForm.get(FORM_CASE.LOCATION_CODE).updateValueAndValidity();
      }
    });
  }

  onSubmitForm() {
    this.formSubmit = true;
    if (this.caseForm.valid) {
      const body: CasePayload = this.builBodyRequest(this.caseForm);
      if (body) {
        const caseList = { caseList: [body] };
        this.subs.add(
          this.caseService.createCase(caseList).subscribe(
            (result) => {
              this.showSuccessModal(this.modalSucces);
              this.resetForm();
              this.getCases();
            },
            (error) => {
              this.errorHandler.showError(error);
            }
          )
        );
      }
    }
  }

  private builBodyRequest(f: FormGroup): CasePayload {
    const body = new CasePayload();
    body.type = f.controls[FORM_CASE.CATEGORY].value;
    body.recordTypeDeveloperName = f.controls[FORM_CASE.TYPE_CASE].value;
    body.description = f.controls[FORM_CASE.COMENTS].value;
    body.subject = f.controls[FORM_CASE.SUBJECT].value;
    switch (f.controls.typeCase.value) {
      case CASES.GEN_INFORMATION:
      case CASES.HIRING:
        body.productExtId = f.controls[FORM_CASE.PRODUCT].value;
        break;
      case CASES.CONTRACTUAL_MODIF:
        switch (f.controls[FORM_CASE.CATEGORY].value) {
          case CASE_CONTRACTUAL_MODIF.NAME_AND_SURNAME:
            body.newName = f.controls[FORM_CASE.NAME].value || null;
            body.newLastName = f.controls[FORM_CASE.FIRST_SURNAME].value || null;
            body.newSecondLastName = f.controls[FORM_CASE.SECONDARY_SURNAME].value || null;
            break;
          case CASE_CONTRACTUAL_MODIF.CONTACT_DETAILS_PHONE_MAIL:
            body.newPhone = f.controls[FORM_CASE.PHONE].value || null;
            body.newEmail = f.controls[FORM_CASE.EMAIL].value || null;
            if (this.emailOrPhoneAtLeastOneError) {
              return null;
            }
            break;
          case CASE_CONTRACTUAL_MODIF.LABOR_DATA:
            body.employmentStatus = f.controls[FORM_CASE.EMPLOYMENT_SITUATION].value || null;
            body.CNOExtId = f.controls[FORM_CASE.CNO].value || null;
            body.CNAEExtId = f.controls[FORM_CASE.CNAE].value || null;
            body.freelanceStartDate =
              formatDate(f.controls[FORM_CASE.FREELANCE_DATE_REGISTER].value, 'yyy-MM-dd', 'es') ||
              null;
            break;
          case CASE_CONTRACTUAL_MODIF.ADDRESS_CHANGES:
            body.addressType = f.controls[FORM_CASE.ADDRESS_TYPE].value || null;
            body.streetType = f.controls[FORM_CASE.STREET_TYPE].value || null;
            body.streetName = f.controls[FORM_CASE.STREET_NAME].value || null;
            body.streetNumber = f.controls[FORM_CASE.STREET_NUMBER].value || null;
            body.portal = f.controls[FORM_CASE.PORTAL].value || null;
            body.floor = f.controls[FORM_CASE.FLOOR].value || null;
            body.door = f.controls[FORM_CASE.DOOR].value || null;
            body.postalCode = f.controls[FORM_CASE.POSTAL_CODE].value || null;
            body.locationCode = f.controls[FORM_CASE.LOCATION_CODE].value || null;
            body.provinceCode = body.postalCode.substr(0, 2);
            break;
          case CASE_CONTRACTUAL_MODIF.PRIVACY_POLICY_OR_CONSENTS:
            body.allowCommercialCommunications =
              f.controls[FORM_CASE.OFFER_COMUNICATIONS].value === 'SI' ? true : false;
            body.allowTransferOfPersonalData =
              f.controls[FORM_CASE.TRANSFER_DATA].value === 'SI' ? true : false;
            break;
          case CASE_CONTRACTUAL_MODIF.BANK_ACCOUNT_CHANGES:
            body.newIBAN = f.controls[FORM_CASE.IBAN].value || null;
            break;
          default:
            break;
        }
        break;
      case CASES.DOCUMENTS:
        body.document = f.controls[FORM_CASE.DOCUMENT_TYPE].value;
        break;

      case CASES.CANCELLATIONS:
        body.newIBAN = f.controls[FORM_CASE.IBAN].value || null;
        body.motive = f.controls[FORM_CASE.REASON_CANCELLATION].value || null;
        if (f.controls[FORM_CASE.CATEGORY].value === CASE_CANCELLATIONS.CANCELLATION_OF_TAXATION) {
          body.impositionNumber = f.controls[FORM_CASE.CANCELLATION_TAXATION].value || null;
        }
        break;

      case CASES.CUSTOMER_AREA:
        body.newIBAN = f.controls[FORM_CASE.IBAN].value || null;
        break;

      case CASES.CLAIMS:
      default:
        break;
    }
    return body;
  }

  private resetForm() {
    this.caseForm.reset();
    this.caseTypeSelected = null;
    this.createForm();
    this.formSubmit = false;
  }

  get isTypeCaseSelected() {
    return false;
  }

  trackByCode(index: number, item: Cnogroupcnocnae) {
    return index && item.code;
  }

  private listenEmploymentSituationChanges() {
    const employmentSituation = this.caseForm.get(FORM_CASE.EMPLOYMENT_SITUATION);
    const cnae = this.caseForm.get(FORM_CASE.CNAE);
    const cno = this.caseForm.get(FORM_CASE.CNO);
    this.subs.add(
      employmentSituation.valueChanges.pipe(distinctUntilChanged()).subscribe((value: string) => {
        if (value !== '01' && value !== '02') {
          cnae.disable();
          cno.disable();
          cnae.setValue(null);
          cno.setValue(null);
        } else {
          cnae.enable();
          cno.enable();
        }
      })
    );
  }

  private listenIbanChanges() {
    const iban = this.caseForm.get(FORM_CASE.IBAN);
    const prefix = 'ES';
    this.subs.add(
      iban.valueChanges
        .pipe(
          filter((value: string) => value?.length > 0 && !value.startsWith(prefix)),
          delay(1)
        )
        .subscribe(() => {
          this.caseForm.patchValue({ iban: prefix }, { emitEvent: false });
        })
    );
  }

  private createState() {
    return combineLatest([
      this.globalPositionService.globalPosition$,
      this.globalPositionService.globalPosition$.pipe(findAccount()),
      this.accountService.currentCustomer$,
    ]).pipe(
      map(([globalPosition, account, currentCustomer]) => ({
        globalPosition,
        account,
        currentCustomer,
      }))
    );
  }

  private setStreamValues() {
    this.employmentSituation$ = this.dictionaryService.getEmploymentStatus();
    this.cno$ = this.dictionaryService.getCNO();
    this.cnae$ = this.dictionaryService.getCNAE();
  }

  hasError(control: AbstractControl) {
    return (control.touched && control.invalid) || (this.formSubmit && control.invalid);
  }

  showNewCasesMobile() {
    this.newCaseMobile = !this.newCaseMobile;
  }

  showSuccessModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template);
  }

  ngOnDestroy() {
    this.globalPositionService.destroy();
    this.subs.unsubscribe();
  }
}
