import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-collapse-case',
  templateUrl: './collapse-case.component.html',
  styleUrls: ['./collapse-case.component.scss'],
})
export class CollapseCaseComponent implements OnInit {
  @Input() caseDate: string;
  @Input() caseType: string;
  @Input() description: string;
  @Input() comentsBackoffice?: string;
  isOpen = false;
  constructor() {}

  ngOnInit(): void {}

  open() {
    this.isOpen = !this.isOpen;
  }
}
