import { Option } from './../models/option';
import { InjectionToken } from '@angular/core';

export enum REASON_CANCELLATIONT {
  NEED_MONEY = 1,
  OFFERED_BETTER_CONDITIONS = 2,
  NOT_HAPPY_WITH_SERVICE = 3,
}

export type ReasonCancellationtType =
  | REASON_CANCELLATIONT.NEED_MONEY
  | REASON_CANCELLATIONT.OFFERED_BETTER_CONDITIONS
  | REASON_CANCELLATIONT.NOT_HAPPY_WITH_SERVICE;

export const reasonCancellationtList: Option[] =  [
  {
    label: 'case_cancel_need_money',
    value: REASON_CANCELLATIONT.NEED_MONEY,
  },
  {
    label: 'case_cancel_offered_better_conditions',
    value: REASON_CANCELLATIONT.OFFERED_BETTER_CONDITIONS,
  },
  {
    label: 'case_cancel_not_happy_with_service',
    value: REASON_CANCELLATIONT.NOT_HAPPY_WITH_SERVICE,
  },
];

export const REASON_CANCELLATIONT_LIST = new InjectionToken('REASON_CANCELLATIONT_LIST', {
  providedIn: 'root',
  factory: () => reasonCancellationtList,
});
