import { ADDRESS_TYPE_ENUM } from './../config/address-type';
import { InjectionToken } from '@angular/core';

export const addressTypes = [
  {
    code: ADDRESS_TYPE_ENUM.FISCAL_ADDRESS,
    name: 'Dirección fiscal',
  },
  {
    code: ADDRESS_TYPE_ENUM.POSTAL_ADDRESS,
    name: 'Dirección postal',
  }
]

export type AddressTypes = typeof addressTypes;

export const ADDRESS_TYPES = new InjectionToken('ADDRESS_TYPES', {
  providedIn: 'root',
  factory: () => addressTypes,
});
